body {
  letter-spacing: .5px;
  margin: 16px;
  font-family: sans-serif;
  line-height: 1.5;
}

#search-box {
  max-width: 320px;
  font: inherit;
  outline-offset: 3px;
  background-color: #e2e2e2;
  border: none;
  border-radius: 10px;
  outline: 2px solid #5b5b5b;
  margin-bottom: 24px;
  padding: 10px 1rem;
  font-size: 15px;
  transition: all .25s;
}

.country-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-list li {
  align-items: center;
  padding-bottom: 10px;
  font-size: 22px;
  font-weight: 500;
  display: flex;
}

.country-list img {
  margin-right: 10px;
}

.country-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

